document.addEventListener("DOMContentLoaded", function () {
	const myForms = document.querySelectorAll(".js-myform");

	myForms.forEach((form) => {
		function toggleLoader() {
			const loaderAll = document.querySelectorAll(".js-loader");
			loaderAll.forEach((loader) => {
				loader.classList.toggle("hidden");
			});
		}

		function onSuccess(formNode) {
			alert("Ваша заявка успешно отправлена!");
			formNode.classList.toggle("hidden");
		}

		function serializeForm(formNode) {
			const data = new FormData(formNode);
			return data;
		}
		async function sendData(data) {
			return await fetch("/api/apply/", {
				method: "POST",
				headers: { "Content-Type": "multipart/form-data" },
				body: data,
			});

			// Чтобы проверить, как работает
			// обработка ответа, можно использовать
			// поддельный ответ от сервера:

			// return new Promise(resolve => {
			//   setTimeout(() => {
			//     resolve({
			//       status: 400,
			//       error: {
			//         message: 'Что-то пошло не так!'
			//       }
			//     })
			//   })
			// })
		}

		form.addEventListener("submit", handleFormSubmit);

		async function handleFormSubmit(event) {
			event.preventDefault();
			event.target.reset();

			const data = serializeForm(event.target);

			toggleLoader();
			const { status } = await sendData(data);
			toggleLoader();

			if (status === 200) {
				onSuccess(event.target);
			}
		}
	});
});
