document.addEventListener("DOMContentLoaded", () => {
	// DOM готов к взаимодейтсвию

	const onScrollHeader = () => {
		// объявляем основную функцию onScrollHeader

		const header = document.querySelector(".js-scroll"); // находим header и записываем в константу
		let prevScroll = window.pageYOffset; // узнаем на сколько была прокручена страница ранее
		let currentScroll; // на сколько прокручена страница сейчас (пока нет значения)

		window.addEventListener("scroll", () => {
			// при прокрутке страницы

			if (window.scrollY > 250) {
				header.classList.add("m-header__scroll");
			} else {
				header.classList.remove("m-header__scroll");
			}

			currentScroll = window.pageYOffset; // узнаем на сколько прокрутили страницу

			const headerHidden = () => header.classList.contains("m-header__hidden"); // узнаем скрыт header или нет

			if (currentScroll > prevScroll && !headerHidden()) {
				// если прокручиваем страницу вниз и header не скрыт
				header.classList.add("m-header__hidden"); // то скрываем header
			}
			if (currentScroll < prevScroll && headerHidden()) {
				// если прокручиваем страницу вверх и header скрыт
				header.classList.remove("m-header__hidden"); // то отображаем header
			}

			prevScroll = currentScroll; // записываем на сколько прокручена страница на данный момент
		});
	};

	onScrollHeader(); // вызываем основную функцию onScrollHeader
});
