const mobileBtn = document.querySelector(".js-mobile-btn");
const rightsideMenu = document.querySelector(".js-rightside-menu");
const rightsideMenuClose = document.querySelector(".js-rightside-menu__close");

mobileBtn.addEventListener("click", function () {
	rightsideMenu.classList.add("rightside-menu--open");
});
rightsideMenuClose.addEventListener("click", function () {
	rightsideMenu.classList.remove("rightside-menu--open");
});
